@import-normalize; /* bring in normalize.css styles */

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap');

:root {
  --black-2: rgba(0, 0, 0, 0.251);
  --black: rgba(0, 0, 0, 1);
  --caribbean-green: rgba(36, 204, 167, 1);
  --royal-blue: rgba(74, 86, 226, 1);
  --quick-silver: rgba(166, 166, 166, 1);
  --silver: rgba(189, 189, 189, 1);
  --bon-jour: rgba(224, 224, 224, 1);
  --mystic: rgba(231, 234, 242, 1);
  --strawberry: rgba(255, 100, 150, 1);
  --white-3: rgba(255, 255, 255, 0.2);
  --white-2: rgba(255, 255, 255, 0.4);
  --white: rgba(255, 255, 255, 1);
  --font-size-xs: 12px;
  --font-size-s: 14px;
  --font-size-m: 16px;
  --font-size-1: 18px;
  --font-size-x1: 30px;
  --font-family-circe-bold: 'Circe-Bold';
  --font-family-circe-regular: 'Circe-Regular';
  --font-family-poppins: 'Poppins';
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e7eaf2;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

li {
  list-style: none;
}

a:-webkit-any-link {
  cursor: pointer;
  text-decoration: none;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
  background-color: #4A56E2;
  border-radius: 5px;
}

@media screen and (min-width: 768px) {
  body {
    background-image: url(../src/images/LoginPage/login-elipse-purple.png),
      url(../src/images/LoginPage/login-elipse-pink.png);
    background-repeat: no-repeat, no-repeat;
    background-position: bottom left, top right;
    backdrop-filter: blur(25px);
    height: 100%;
  }
}
